@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --base-font-family: "zen old mincho", serif;
  --second-font-family: "cormorant garamond", serif;
  --padding-pc: 25px;
  --padding-sp: 20px;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.inner {
  max-width: 90.625rem;
  padding: 0 var(--padding-pc);
  margin-inline: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: 37.5rem;
    padding: 0 var(--padding-sp);
  }
}

.top-hero {
  position: relative;
  padding: 3rem 0 9rem;
  background: linear-gradient(180deg, #f8f6f5 0%, #f3dede 48%, #dca0a2 100%);
  overflow: hidden;
}

.top-hero__background {
  position: absolute;
  top: 2.5rem;
  left: 2.5rem;
  width: 75.75rem;
  max-width: 64%;
  height: 51.75rem;
  border-radius: 2.5rem;
  overflow: hidden;
  box-shadow: 0 1.875rem 5rem rgba(40, 22, 25, 0.25);
}

.top-hero__background-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-hero__petal {
  position: absolute;
  pointer-events: none;
  display: block;
}

.top-hero__petal--left {
  top: 6rem;
  left: -6.5rem;
  width: 26.25rem;
  opacity: 0.9;
}

.top-hero__petal--right {
  top: 9rem;
  right: -2.5rem;
  width: 22.5rem;
  opacity: 0.75;
}

.top-hero__petal--bottom {
  right: -8.75rem;
  bottom: -6rem;
  width: 35rem;
  opacity: 0.65;
}

.top-hero__logo {
  position: relative;
  z-index: 3;
  margin-left: 3.5rem;
  display: inline-flex;
  flex-direction: column;
  color: #371213;
  font-family: var(--second-font-family);
  letter-spacing: 0.1em;
}

.top-hero__logo-main {
  margin: 0;
  font-size: 2.5rem;
  font-weight: var(--medium);
  line-height: 1;
}

.top-hero__logo-sub {
  margin: 0.25rem 0 0;
  font-size: 1rem;
  font-weight: var(--regular);
  line-height: 1.5;
  letter-spacing: 0.3em;
}

.top-hero__visual {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  margin: -2.5rem auto 0;
  max-width: 45rem;
}

.top-hero__model {
  display: block;
  width: 100%;
  max-width: 44.375rem;
  filter: drop-shadow(0 1.5rem 3.75rem rgba(40, 22, 25, 0.25));
}

.top-hero__badge {
  position: absolute;
  right: 3rem;
  width: 20rem;
  height: 19rem;
  border-radius: 2.5rem;
  overflow: hidden;
  box-shadow: 0 1.5rem 3rem rgba(40, 22, 25, 0.25);
  z-index: 3;
}

.top-hero__badge--about {
  top: 3rem;
}

.top-hero__badge--menu {
  top: 24rem;
}

.top-hero__badge-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-hero__badge-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  font-family: var(--second-font-family);
  font-size: 3rem;
  font-weight: var(--regular);
  letter-spacing: 0.08em;
  color: var(--white);
  text-shadow: 0 0 1.25rem rgba(55, 18, 19, 0.55);
}

.top-hero__vertical-copy {
  position: absolute;
  top: 50%;
  left: 17rem;
  display: flex;
  gap: 0.75rem;
  transform: translateY(-50%);
  z-index: 3;
}

.top-hero__vertical-copy-text {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 1.875rem;
  font-weight: var(--regular);
  letter-spacing: 0.1em;
  line-height: 1.3;
  color: var(--white);
  text-shadow: 0 0 1.125rem rgba(16, 16, 16, 0.35);
}

.top-hero__slogan {
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: center;
}

.top-hero__slogan-text {
  margin: 0;
  font-family: var(--second-font-family);
  font-size: clamp(3rem, 7vw, 7.5rem);
  font-weight: var(--regular);
  letter-spacing: 0.08em;
  line-height: 1.1;
  color: rgba(255, 208, 210, 0.72);
}

.top-hero__nav {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}

.top-hero__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 2.5rem;
  align-items: center;
}

.top-hero__nav-item {
  font-size: 1.125rem;
}

.top-hero__nav-link {
  font-size: 1.125rem;
  font-weight: var(--regular);
  letter-spacing: 0.12em;
  color: #25201b;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.top-intro {
  padding: 7.5rem 0 10rem;
  background: linear-gradient(180deg, #f0d1d2 0%, #f5dcdc 38%, #f8f1f1 100%);
}

.top-intro__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  text-align: center;
}

.top-intro__title {
  margin: 0;
  font-size: 2rem;
  letter-spacing: 0.12em;
  line-height: 1.4;
  color: #25201b;
}

.top-intro__description {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 45rem;
}

.top-intro__description-text {
  margin: 0;
  font-size: 1.125rem;
  line-height: 2.1;
  letter-spacing: 0.06em;
  color: #25201b;
}

.top-intro__gallery {
  display: flex;
  gap: 2rem;
  justify-content: center;
  width: 100%;
}

.top-intro__card {
  margin: 0;
  width: 20rem;
  max-width: 100%;
  height: 22.5rem;
  border-radius: 2.5rem;
  overflow: hidden;
  box-shadow: 0 1.25rem 2.5rem rgba(40, 22, 25, 0.18);
}

.top-intro__card-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (any-hover: hover) {
  .top-hero__nav-link:hover {
    opacity: 0.65;
  }
}
@media screen and (max-width: 767px) {
  .top-hero {
    padding: 2rem 0 6rem;
  }
  .top-hero__background {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    max-width: none;
    margin: 0 auto;
    border-radius: 2rem;
    box-shadow: 0 1rem 2.5rem rgba(40, 22, 25, 0.2);
  }
  .top-hero__petal--left {
    top: 2rem;
    left: -5rem;
    width: 15rem;
  }
  .top-hero__petal--right {
    top: auto;
    bottom: 10rem;
    right: -3.5rem;
    width: 13.75rem;
  }
  .top-hero__petal--bottom {
    right: -7.5rem;
    bottom: -7.5rem;
    width: 20rem;
  }
  .top-hero__logo {
    margin-left: 0;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
  }
  .top-hero__visual {
    margin: 1.5rem auto 0;
    max-width: 26.25rem;
  }
  .top-hero__badge {
    position: relative;
    right: auto;
    top: auto;
    width: 100%;
    max-width: 20rem;
    height: auto;
    aspect-ratio: 320/304;
    margin: 2rem auto 0;
  }
  .top-hero__badge-label {
    font-size: 2.25rem;
  }
  .top-hero__vertical-copy {
    position: relative;
    top: auto;
    left: auto;
    transform: none;
    justify-content: center;
    margin: 2rem auto 0;
  }
  .top-hero__vertical-copy-text {
    font-size: 1.5rem;
  }
  .top-hero__slogan {
    position: relative;
    top: auto;
    left: auto;
    transform: none;
    margin: 2rem auto 0;
  }
  .top-hero__nav {
    position: relative;
    bottom: auto;
    left: auto;
    transform: none;
    margin: 2.5rem auto 0;
    width: 100%;
  }
  .top-hero__nav-list {
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
  }
  .top-hero__nav-item {
    font-size: 1rem;
  }
  .top-hero__nav-link {
    font-size: 1rem;
  }
  .top-intro {
    padding: 5rem 0 6rem;
  }
  .top-intro__inner {
    gap: 2.5rem;
  }
  .top-intro__title {
    font-size: 1.625rem;
  }
  .top-intro__description {
    gap: 1rem;
  }
  .top-intro__description-text {
    font-size: 1rem;
  }
  .top-intro__gallery {
    flex-wrap: wrap;
    gap: 1.25rem;
  }
  .top-intro__card {
    width: calc(50% - 0.75rem);
    height: 13.75rem;
  }
}